<template>
  <div class="forgetPass">
    <van-nav-bar title="忘记密码" left-text="返回" @click-left="goBack('Register')" />
    <div class="box">
      <div v-if="!isPass">
        <van-form>
          <van-field v-model="userData.email" type="text" label="邮箱" placeholder="邮箱地址" />
          <van-field v-model="userData.code" key="code" type="text" label="验证码" placeholder="6位验证码">
            <template #button>
              <van-button size="small" type="info" @click="getCode" :disabled="isSend">{{tip}}</van-button>
            </template>
          </van-field>
          <div class="next-btn">
            <van-button round block type="info" native-type="submit" @click="next">下一步</van-button>
          </div>
        </van-form>
      </div>
      <div v-else>
        <van-form @submit="submit">
          <van-field
            v-model="msg.phone"
            type="text"
            label="手机号"
            placeholder="注册时手机号"
            left-icon="phone-o"
            maxlength="11"
          />
          <van-field
            v-model="msg.password"
            :type="isShow?'password':'text'"
            label="新密码"
            placeholder="字母数字_组合(6-16位)"
            left-icon="lock"
            @click-right-icon="isToggleShow"
            maxlength="16"
          >
            <van-icon
              class="register-icon fa"
              slot="right-icon"
              :class="[isShow?'fa-eye-slash':'fa-eye']"
            />
          </van-field>
          <div class="next-btn">
            <van-button round block type="info" native-type="submit">提交</van-button>
          </div>
        </van-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tip: "发送验证码",
      isSend: false,
      isPass: false,
      isShow: true,
      msg: {
        phone: "",
        password: ""
      },
      userData: {
        email: "",
        code: ""
      },
      emailReg: {
        reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
        msg: "邮箱格式不正确"
      },
      userReg: [
        {
          reg: /^1[3-9]\d{9}$/,
          msg: "手机号格式不正确"
        },
        {
          reg: /^\w{6,16}$/,
          msg: "密码格式不正确"
        }
      ]
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 改变密码框输入类型
    isToggleShow() {
      this.isShow = !this.isShow;
    },
    //下一步
    next() {
      // 判断邮箱格式是否正确
      if (!this.emailReg.reg.test(this.userData.email)) {
        this.$toast(this.emailReg.msg);
        return;
      }
      //发起发送验证码请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "post",
        url: "/checkValidCode",
        data: {
          appkey: this.appkey,
          validCode: this.userData.code
        }
      })
        .then(result => {
          this.$toast.clear();
          if (result.data.code == "K001") {
            this.isPass = true;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    //发送验证码
    getCode() {
      // 判断邮箱格式是否正确
      if (!this.emailReg.reg.test(this.userData.email)) {
        this.$toast(this.emailReg.msg);
        return;
      }
      //获取邮箱验证码
      let time = 60;
      this.tip = time + "秒后重新发送";
      this.isSend = true;
      let timer = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          timer = null;
          this.tip = "发送验证码";
          this.isSend = false;
        } else {
          time--;
          this.tip = time + "秒后重新发送";
        }
      }, 1000);

      //发起发送验证码请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "post",
        url: "/emailValidCode",
        data: {
          appkey: this.appkey,
          email: this.userData.email
        }
      })
        .then(result => {
          this.$toast.clear();
          this.$toast(result.data.msg);
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    //找回密码
    submit() {
      // 判断手机号密码格式是否有误
      if (!this.userReg[0].reg.test(this.msg.phone)) {
        this.$toast(this.userReg[0].msg);
        return;
      }
      if (!this.userReg[1].reg.test(this.msg.password)) {
        this.$toast(this.userReg[1].msg);
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "post",
        url: "/retrievePassword",
        data: {
          appkey: this.appkey,
          phone: this.msg.phone,
          password: this.msg.password
        }
      })
        .then(result => {
          this.$toast.clear();
          if (result.data.code == "L001") {
            this.$router.push({ name: "Login" });
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    }
  }
};
</script>

<style lang="less" scoped>
.forgetPass {
  /deep/ .van-nav-bar__text {
    color: slateblue;
    border-left: 1px dashed #eee;
    border-top: 1px dashed #eee;
    border-radius: 13px;
    padding: 2px 8px;
    box-shadow: 2px 2px 2px #ddd;
    line-height: 25px;
  }
  /deep/ .van-button--info {
    background-image: linear-gradient(to left, slateblue, steelblue);
    border: none;
  }
  .box {
    padding: 0 5px;
    .next-btn {
      margin-top: 20px;
    }
  }
}
</style>